
import BarChart from '@/components/demo/BarChart';
import PieChart from '@/components/demo/PieChart';
import RadarChart from '@/components/demo/RadarChart';
import InfoBar from '@/components/demo/InfoBar';
import InfoPie from '@/components/demo/InfoPie';
import InfoRadar from '@/components/demo/InfoRadar';
import Scroller from 'vux/src/components/scroller/index.vue'

import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: "ocean",
data() {
return {
tableData: [],
cardount: '',
currentView: 'InfoBar', // 默认选中第一项
eChartsView: 'BarChart',
active: 0,
move: 0,
eCheight: 60, //echarts 高度
show: true,
show2: false,
name: 'bbbb',
name2:'cccc', //父传子 保留修改margine
smHight:0,
curHeight: 0,// 手机高度
objeChar:'',
objiFor:'',
objJob:'',
ua:'',
inShow:false, //关于弹层
moveS:'no',//滑动变量
tabs: [
{
type: '能力画像',
view: 'InfoBar',
view2: 'BarChart',
class1: 'icon-zhengjian'
},
{
type: '能力图谱',
view: 'InfoPie',
view2: 'PieChart',
class1: 'icon-dengpao1'
},
{
type: '职业匹配',
view: 'InfoRadar',
view2: 'RadarChart',
class1: 'icon-zhiyeshengyamubiao'
}
]
}
},
directives: {
TransferDom
},
components: {
BarChart,  //柱状
PieChart,  //饼状
RadarChart, //雷达
InfoBar,
InfoPie,
InfoRadar,
Scroller,
XDialog,
XButton,
Group,
XSwitch,
Alert,
},
mounted() {
var ua = navigator.userAgent.toLowerCase();   
if (/iphone|ipad|ipod/.test(ua)) {
this.ua = 'ios'
$('.activel').css('margin-top','-8px')
console.log($('.activel'))
} else if (/android/.test(ua)) {
this.ua = '安卓'

}

this.getcurHeight()
this.getdata()

this.objeChar = document.getElementById('eCharts')
this.objiFor = document.getElementById('xx1')
this.$nextTick(()=>{
if(document.querySelector('.xxx3')){
console.log('zheshi sha ?')
}
})
},
methods: {
/**
* 获取数据
*/
getdata() {
this.$axios.get('http://192.168.1.230/api/Job/special').then((response) => {
this.tableData = response.data.data
this.cardount = (this.tableData.length) * 250 + (this.tableData.length) * 10 + (10)  
console.log(this.tableData)
}).catch((response) => {
console.log(response)
})
},
/**
* 获取屏幕高度
*/
getcurHeight() {
let h = document.documentElement.clientHeight || document.body.clientHeight;
let objeChar = document.getElementById('eCharts')
let objiFor = document.getElementById('xx1')
this.curHeight = h;
console.log(this.curHeight,'gaogoagaoggaoo')
if(this.curHeight<=568){
this.smHight = 1
console.log('iphone5')
objeChar.style.height='50vh'
objiFor.style.height = 'calc(50vh - 44px)'
}else if(667<=this.curHeight&&this.curHeight<=736){
console.log('iphone6 ')
objeChar.style.height='45vh'
objiFor.style.height = 'calc(55vh - 44px)'
this.smHight = 2
}else if(this.curHeight>736){
console.log('iphoneXXXX')
this.smHight = 0
}
},
/**
* 点击tab切换事件
*/

tabChange(i, tab1, tab2) {
if( this.ua === 'ios'){   //判断是ios样式调整
setTimeout(function () {
$('.activel').css('margin-top','-8px')
},10)
}
this.active = i
this.currentView = tab1;
this.eChartsView = tab2;

if(this.smHight === 1){ //iphone5以下
this.objeChar.style.height='50vh';
this.objiFor.style.height = 'calc(50vh - 44px)'
}else if (this.smHight === 2){ //iphone6
this.objeChar.style.height='45vh';
this.objiFor.style.height = 'calc(55vh - 44px)'
}

this.move = 0;
this.show2 = false;
this.name = 'bbbb';
this.name2 = 'cccc'
},
/**
* 滑动事件
*/
swiperup: function () {
if(this.smHight === 1){
this.objeChar.style.height='30vh';
this.objiFor.style.height = 'calc(40vh - 42px)';
setTimeout(function () {
let xx = document.getElementById('xx3')
this.objJob = document.getElementById('xx3')
this.objJob.style.height = '30vh'
},50)
}else if(this.smHight === 2){
this.objeChar.style.height='40vh';
this.objiFor.style.height = 'calc(30vh - 42px)';
setTimeout(function () {
this.objJob = document.getElementById('xx3')
this.objJob.style.height = '30vh'
},50)
}
this.move = 1;
this.show2 = true;
this.name = 'ffff';
this.name2 = 'eeee';
console.log(this.name2)
},
},
watch: {
moveS: function (yes, no) {


},
},
}




